export function SkillDisplay(props){
    const {skills, levels} = props;
    const skillKeys = Object.keys(skills);
    return <div style={{display: 'flex', flexDirection: 'row', flexWrap: 'wrap'}}>
        {skillKeys.map(x => {
            let borderColor = "black";
            let title = x;
            if(levels && levels[x]){
                borderColor = levels[x] >= skills[x] ? 'green' : 'red';
                title += "\n" + "Current level: " + levels[x];
            }
            return <div style={{display: 'flex', padding: "4px", margin: "4px", border: '2px solid ' + borderColor, borderRadius: '4px'}}>
                <SkillIcon skill={x} title={title}/>
                <span>{skills[x]}</span>
            </div>
        })}
    </div>
}

export function SkillIcon(props){
    const {skill} = props;
    return <img src={urls[skill]} alt={skill} title={props.title || skill} style={{width: "16px", height: "16px"}}/>
}